import TestComponent from '../components/TestComponent.vue';

export default {
    layout: 'TestLayout',
    components: {
        TestComponent
    },
    created() {
        this.$nuxt.$on('addComponent', () => {
            this.list.push("추가하는 컴포넌트");
        });
        this.$nuxt.$on('removeComponent', (index) => {
            this.list.splice(index, 1);
        });
    },
    data() {
        return{
            list: [
                'A 테스트 컴포넌트',
                'B 테스트 컴포넌트',
                'C 테스트 컴포넌트',
                'D 테스트 컴포넌트',
                'E 테스트 컴포넌트',
            ]
        }     
    },
    methods: {

    },
}
